<template>
  <div class="container">
    <div class="row mt-4">
      <div class="col-12">

        <app-form :pageHeader="pageHeader"
            :id="id"
            :submitButtonName="submitButtonName"
            :isActive="isActive"
            ref="appForm"
            @submit="submit">
        </app-form>

      </div>
    </div>
  </div>
</template>

<script>
import AppForm from '@/components/form/AppForm';

export default {
  data: () => {
    return {
      id: '',
      pageHeader: 'Create App',
      submitButtonName: 'Create',
      isActive: true,
      failMessage: '',
      successMessage: 'Create app success!'
    };
  },
  methods: {
    submit (value) {
      this.setDefault();

      // create app
      this.appName = value.appName;
      this.remark = value.remark;
      const json = { appName: this.appName, remark: this.remark };
      this.isActive = false;
      this.axios.post(process.env.VUE_APP_SERVICE_HOST + '/app/create', json)
        .then(response => {
          this.$refs.appForm.showSuccess(this.successMessage);
        })
        .catch(error => {
          console.log('catch error = ' + error);
          this.isActive = true;
          if (error.response) {
            console.log('error.response=' + error.response);
            const data = error.response.data;
            this.failMessage = (data.topic === undefined) ? 'Something went wrong' : data.topic;
            this.$refs.appForm.setFieldError(data.fields);
          } else {
            console.log('error=' + error);
            this.failMessage = error.message;
          }
          this.$refs.appForm.showFail(this.failMessage);
        });
    },
    setDefault () {
      // set default before create

    }
  },
  components: {
    AppForm
  },
  computed: {

  }
};
</script>

